import React, { useState } from 'react';
import '../styles/quiz.css'; // Import the CSS file

const qna = {
  'Ai l ngi ch ng lm quen trc?': ['Ch r', 'C du'],
  'Ln u tin chng mnh gp nhau l  u?': ['Hannover', 'Essen', 'Berlin', 'H Ni'],
  'C du khi mi yu nhau hay gi ch r l g?': ['Ch', 'Anh', 'Tnh yu', 't'],
};

const Quiz = () => {
  const [answers, setAnswers] = useState([]);

  const handleAnswer = (question, selectedAnswer) => {
    const correctAnswer = qna[question][0];
    const isCorrect = selectedAnswer === correctAnswer;
    setAnswers((prevAnswers) => [...prevAnswers, { question, selectedAnswer, isCorrect }]);

    // Scroll down to the bottom
    window.scrollTo({
      top: document.body.scrollHeight,
      behavior: 'smooth',
    });
  };

  return (
    <div className="quiz-container">
      <h2>Mt vi facts v chng mnh</h2>
      {Object.entries(qna).map(([question, choices]) => (
        <div key={question} className="question-box">
          <p className="question">{question}</p>
          {choices.map((choice, index) => (
            <button
              key={choice}
              onClick={() => handleAnswer(question, choice)}
              className="answer-button"
            >
              {choice}
            </button>
          ))}
        </div>
      ))}
      <h3 className="results">p n:</h3>
      {answers.map((answer, index) => (
        <div key={index} className={answer.isCorrect ? 'correct-answer' : 'wrong-answer'}>
          {answer.isCorrect ? (
            <p>Chnh xc! {answer.selectedAnswer} l p n ng .</p>
          ) : (
            <p>
              Whoopsie! Bn chn {answer.selectedAnswer}, nhng m p n l {qna[answer.question][0]} c.
            </p>
          )}
        </div>
      ))}
    </div>
  );
};

export default Quiz;
